<template>
  <div class="container">
    <van-nav-bar title="小智" fixed left-arrow @click-left="$router.back()"></van-nav-bar>
    <div class="chat-list" ref="list">
      <div v-for="(item,index) in list" :key="index" :class="{'chat-item':true,left:item.name==='xz',right:item.name!=='xz'}">
        <!-- 如果是左边的就让第一个van-image显示 -->
        <van-image fit="cover" round :src="XzImg" v-if="item.name==='xz'" />
        <div class="chat-pao">{{item.msg}}</div>
        <!-- 如果是右边的就让第二个van-image显示 -->
        <van-image  fit="cover" round :src="photo" v-if="item.name!=='xz'" />
      </div>
      <!-- <div class="chat-item right">
        <div class="chat-pao">ewqewq</div>
        <van-image  fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
      </div> -->
    </div>
    <div class="reply-container van-hairline--top">
      <van-field v-model.trim="value" placeholder="说点什么...">
        <van-loading v-if="commentLoading" slot="button" type="spinner" size="16px"></van-loading>
        <span v-else @click="send()" slot="button" style="font-size:12px;color:#999">提交</span>
      </van-field>
    </div>
  </div>
</template>

<script>
import XzImg from '@/assets/xz.jpg'
import { mapState } from 'vuex'
import io from 'socket.io-client'
export default {
  data () {
    return {
      value: '',
      commentLoading: false,
      list: [],
      XzImg
    }
  },
  computed: {
    ...mapState(['user', 'photo'])
  },
  created () {
    this.socket = io('http://ttapi.research.itcast.cn', {
      query: {
        token: this.user.token
      }
    })
    this.socket.on('connect', () => {
      console.log('和服务器建立连接成功！！！')
      this.list.push({ msg: '秃头小宝贝', name: 'xz' })
      // 需要监听小智同学回复的消息
      // message的回调参数是data data 是个对象 里面有msg和timestamp
      this.socket.on('message', (data) => {
        this.list.push({ ...data, name: 'xz' })// 记录一下小智发送的信息
        this.scrollBottom()
      })
    })
  },
  methods: {
    scrollBottom () {
      this.$nextTick(() => {
        // console.log(this.$refs.list.scrollHeight, document.documentElement.clientHeight, this.$refs.list.scrollHeight - document.documentElement.clientHeight - 90)

        this.$refs.list.scrollTop = this.$refs.list.scrollHeight - document.documentElement.clientHeight + 100
      })
    },
    send () {
      if (!this.value) return false
      let obj = { name: 'self', msg: this.value, timestamp: Date.now() }
      this.socket.emit('message', obj)
      this.list.push(obj)
      this.value = ''
      this.scrollBottom()
    }
  },
  beforeDestroy () {
    this.socket.close()
  }
}
</script>

<style lang="less" scoped>
.container{
  height:100%;
  width:100%;
  position:absolute;
  top:0;
  left:0;
  box-sizing:border-box;
  background-color: #fafafa;
  padding:46px 0 50px 0;
  .chat-list{
    height:100%;
    overflow-y:scroll;
    .chat-item{
      padding:10px;
      .van-image{
        // vertical-align:top;
        width:40px;
        height:40px;
      }
      .chat-pao{
        vertical-align:top;
        display:inline-block;
        min-width:40px;
        max-width:70%;
        min-height:40px;
        line-height: 38px;
        border:0.5px solid #c2d9ea;
        border-radius:4px;
        padding:0 10px;
        background-color: #e0effb;
        font-size:14px;
        color:#333;
        word-break:break-all;
        position:relative;
        &::before{
          content:'';
          width:10px;
          height:10px;
          position:absolute;
          border-top:0.5px solid #c2d9ea;
          border-right:0.5px solid #c2d9ea;
          background: #e0effb;
          top:12px;
        }
      }

    }
  }
}
.chat-item.right{
  text-align: right;
  .chat-pao{
    margin-right:15px;
    &::before{
      right:-5px;
      transform: rotate(45deg)
    }
  }
}
.chat-item.left{
  text-align:left;
  .chat-pao{
    margin-left:15px;
    margin-right:0;
    &::before{
      left:-5px;
      transform: rotate(-135deg)
    }
  }
}
.reply-container{
  position:fixed;
  left:0;
  bottom:0;
  height:44px;
  width:100%;
  background: #f5f5f5;
  z-index:9999;
}
</style>
